/**
 * COMPONENTE HERO PRODUCTOS
 * Hero section especfico para la pgina de productos
 */

import React from 'react';
import { HeroSection } from '../shared';

interface ProductosHeroProps {
  onScrollToCollection: (e: React.MouseEvent<HTMLAnchorElement>) => void;
}

const ProductosHero: React.FC<ProductosHeroProps> = ({ onScrollToCollection }) => {
  return (
    <div style={{ paddingTop: '60px' }}>
      <HeroSection
        title="Catlogo de Productos"
        subtitle="Diamond Jewelry  San Miguel  Buenos Aires"
        description="Descubre nuestra amplia coleccin de joyera fina y accesorios exclusivos.<br/>Cada pieza seleccionada por su calidad excepcional y diseo nico."
        primaryButton={{
          text: 'Ver Catlogo',
          href: '#coleccion',
          onClick: onScrollToCollection
        }}
        secondaryButton={{
          text: 'Contactar',
          href: '/contacto'
        }}
        backgroundType="gradient"
      />
    </div>
  );
};

export default ProductosHero;
